@import 'tools/_mixins'
@import 'tools/_helpers'
@import 'tools/_media_queries'
@import 'tools/_normalize'
@import 'tools/_settings'



body
  background: black
.active
  display: flex !important
.preloader
  background: black
  position: fixed
  height: 100%
  width: 100%
  display: none
  z-index: 20
  .image
    margin: auto

.container
  @extend .column
  .header
    padding: 10px 0
    background: url('../images/topmenu.jpg') no-repeat center center
    background-size: cover
    position: relative
    border-bottom: 1px solid #4c4d4d
    @extend .row
    justify-content: flex-end
    align-items: center
    .logo
      position: absolute
      left: 50%
      top: 50%
      transform: translate(-50%, -50%)
      img 
        width: 70px
    .ui-icons
      width: 20%
      @extend .row
      justify-content: flex-end
      .flex-item
        margin-right: 15px
      img 
        width: 30px
        height: 30px
      .basket
        position: relative
        .counter
          position: absolute
          bottom: -5px
          left: -5px
          color: white
          background: #E19541
          border-radius: 50%
          width: 20px
          height: 20px
          span 
            position: absolute
            top: 50%
            left: 50%
            transform: translate(-50%, -50%)
  .main 
    height: calc(100vh - 57px)
    .flex-inner
      height: 100%
      @extend .row
      flex-wrap: wrap
      .flex-item
        width: 50%
        height: 33,3%
        &:nth-of-type(1)
          background: url('../images/konkurs.jpg') no-repeat center center
          background-size: cover
        &:nth-of-type(2)
          background: url('../images/kalkulyator.jpg') no-repeat center center
          background-size: cover
        &:nth-of-type(3)
          background: url('../images/mesta.jpg') no-repeat center center
          background-size: cover
        &:nth-of-type(4)
          background: url('../images/recepies.jpg') no-repeat center center
          background-size: cover
        &:nth-of-type(5)
          background: url('../images/call.jpg') no-repeat center center
          background-size: cover
        &:nth-of-type(6)
          background: url('../images/about bbq.jpg') no-repeat center center
          background-size: cover
