@import 'tools/_mixins'
@import 'tools/_helpers'
@import 'tools/_media_queries'
@import 'tools/_normalize'
@import 'tools/_settings'

#competition
  .container
    .header
      justify-content: space-between
      .back
        margin-left: 10px
      .logo
        font-family: 'JackDaniels CondensedBold'
        color: white 
        font-size: 24px
    .main 
      background: url('../images/bg.jpg') repeat-y 
      background-size: cover
      height: calc(100vh - 62px)
      .flex-inner
        height: 100%
        padding-top: 2%
        @extend .column

        .flex-item
          background: transparent
          font-family: 'JackDaniels CondensedBold'
          @extend .row
          align-items: center
          text-align: center
          width: 100%
          justify-content: space-between
          .img
            margin-left: 10%
            img 
              width: 13vh
          .text
            width: 60%
            .title
              font-size: 40px
              line-height: 40px
              
              color: #E19541
              +mobile-small()
                font-size: 25px
                line-height: 25px
            .txt
              color: white
              font-size: 25px
              line-height: 35px

              +mobile-small()
                font-size: 15px
                line-height: 25px
            .small
              font-size: 20px
              line-height: 20px
              +mobile-small()
                font-size: 12px
                line-height: 15px
              
          &:last-of-type
            padding-bottom: 3%
            width: 100%
            .button
              padding: 15px 20px 10px 20px
              background: #E19541
              color: white
              border-radius: 10px
              font-size: 30px
              margin: 0 auto
              +mobile-small()
                font-size: 20px
                padding: 15px 30px 10px 30px




        